@import url(https://fonts.googleapis.com/css?family=Oswald&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  margin: 0;
  padding: 0;
  background-color: rgb(167, 221, 236);
}
.App-logo {
  height: 40vmin;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #09d3ac;
}
.navbar {
  margin: 0 auto;
  padding: 10px;
  background-image: -webkit-gradient(linear, left bottom, left top, from(#004796), to(#00a3de));
  background-image: linear-gradient(to top, #004796, #00a3de);
  width: 100%;
}
.navbar ul {
  display: flex;
  justify-content: center;
  padding: 0;
}
.navbar ul li {
  list-style: none;
  padding: 15px 30px;
  text-align: center;
  display: flex;
  justify-content: center;
}
.link {
  font-size: 21px;
  text-decoration: none;
  color: #fff;
  /* text-transform: uppercase; */
  padding: 10px 20px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
}
.link:hover {
  color: rgb(185, 185, 185);
}

/* MEDIA QUERIES */
@media (max-width: 800px) {
  .link {
    font-size: 20.5px;
  }
}

@media (max-width: 600px) {
  .navbar ul {
    flex-direction: column;
    margin: 0 auto;
  }
  .navbar ul li {
    padding: 5px 0;
    max-width: 100%;
  }
  .link {
    font-size: 19px;
  }
}

@media (max-width: 400px) {
  .navbar ul {
    flex-direction: column;
    margin: 0 auto;
  }
  .navbar ul li {
    list-style: none;
    padding: 5px 0;
    max-width: 100%;
  }
  .link {
    font-size: 17px;
    padding: 10px;
  }
}

